<template>
  <!--  -->
  <van-popup
    class="bg-tran image-cropper"
    v-model="visible"
    closeable
    position="top"
    :lock-scroll="false"
    :style="{ height: '100%' }"
  >
    <div class="flex-column-center height100">
      <vueCropper
        ref="imageCropper"
        :img="url"
        :outputSize="option.outputSize"
        :outputType="option.outputType"
        :info="option.info"
        :full="option.full"
        :autoCropWidth="option.autoCropWidth"
        :autoCropHeight="option.autoCropHeight"
        :canMove="option.canMove"
        :canMoveBox="option.canMoveBox"
        :original="option.original"
        :autoCrop="option.autoCrop"
        :fixed="option.fixed"
        :fixedNumber="option.fixedNumber"
        :centerBox="option.centerBox"
        :infoTrue="option.infoTrue"
        :fixedBox="option.fixedBox"
        :high="option.high"
        :mode="option.mode"
      ></vueCropper>
      <van-col span="24" class="font14 col-white">
        <van-col span="8" class="p-2" @click="onCancel"
          ><span></span></van-col
        >
        <van-col span="8" class="p-2 text-center" @click="rotateImage"
          ><span class="font18"><van-icon name="replay" /></span
        ></van-col>
        <van-col span="8" class="p-2 text-right" @click="onConfirm"
          ><span><i class="el-icon-loading" v-if="loading"></i></span
        ></van-col>
      </van-col>
    </div>
  </van-popup>
</template>
<script>
import { VueCropper } from "vue-cropper";
import axios from "axios";
// const COS = require('cos-js-sdk-v5');

export default {
  components: {
    VueCropper,
  },
  props: {
    visible: Boolean, //
    url: String, //
  },
  data() {
    return {
      loading: false,
      option: {
        outputSize: 0.8,
        info: false, // 
        outputType: "jpeg", // 
        canScale: false, // 
        autoCrop: true, // 
        autoCropWidth: window.innerWidth - 100 + "px", // 
        autoCropHeight: window.innerWidth - 100 + "px", // 
        high: true, // dpr 
        fixedBox: true, //  
        fixed: true, // 
        fixedNumber: [1, 1], // 
        full: true, // 
        canMoveBox: false, // 
        original: false, // 
        centerBox: false, // 
        infoTrue: false, // true  false 
        mode: "100% auto", // 
      },
    };
  },
  methods: {
    rotateImage() {
      //
      this.$refs.imageCropper.rotateRight();
    },
    onCancel() {
      //
      this.$emit("cancel");
    },
    onConfirm() {
      //
      if (this.loading) {
        return;
      }
      this.loading = true;
      this.$refs.imageCropper.getCropBlob((file) => {
        console.log(file);
        //fileCOSdemo
        this.handleChange(file).then(
          (data) => {
            console.log(file);
            // 
            this.$emit("success", file);
            this.loading = false;
            //
          },
          (error) => {
            this.loading = false;
          }
        );
      });
    },
    async handleChange(file) {
      //
      let key = "wUbxngjGDBUEsS-kulRcXh2Jp-9dXkVM";
      let secret = "gxcRJD0wqwVElf2OemeFMLWqLIo9ywNR";
      let image_base64 = file.content;
      let formData = new FormData();
      formData.append("api_key", key);
      formData.append("api_secret", secret);
      formData.append("image_base64", image_base64);
      let scan = await axios({
        url: "https://api-cn.faceplusplus.com/imagepp/v2/generalocr",
        method: "post",
        data: formData,
      });
      console.log(scan);
      
    },
  },
};
</script>
<style>
.font14 {
  color: red;
}
.image-cropper .height100 {
  height: 100vh;
}
.image-cropper .flex-column-center {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
.image-cropper .vue-cropper {
  background: #000;
}
.image-cropper .vue-cropper .cropper-view-box {
  outline: 1px solid #fff !important;
  outline-color: #fff !important;
}
.image-cropper .van-col {
  height: 50px;
  line-height: 50px;
  margin-bottom: 30px;
}
.image-cropper .van-col .el-icon-loading {
  margin-left: 5px;
}
</style>
